body {
    font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}

.jsloex_calculate {
    width: 500px;
    margin: 0 auto;
    text-align: center;
    background-color: aliceblue;
    border: 1px blue solid;
    padding: 30px;
}

.jsloex_calculate .operation_block button {
    height: 40px;
    width: 40px;
    margin: 20px;
}

.inputs_block .input_block label {
    display: block;
}

.inputs_block .input_block {
    display: inline-block;
    padding: 10px;
}